
export default {
  data() {
    return {
      nasa: {},
      message:
        "Image of the day from NASA (" +
        new Date().toLocaleString().slice(0, 10) +
        ")",
      errorMsg: false,
      loading: false,
      keyNasa: "NscA0buKrklyeIAikyUvalzPLnTqlQvJVssJIUgM",
      nasaBaseURL: "https://api.nasa.gov/planetary/",
    };
  },
  created() {
    this.fetchNasaInfo();
  },
  methods: {
    fetchNasaInfo: async function () {
      this.loading = true;
      try {
        const response = await this.axios(
          `${this.nasaBaseURL}apod?api_key=${this.keyNasa}`
        );
        this.nasa = await response.data;
      } catch (error) {
        console.log(error);
        this.errorMsg = true;
      } finally {
        this.loading = false;
      }
    },
  },
};
